import React, { Component } from 'react'
import { spawn } from 'child_process'

import { createWriteStream } from 'fs'

export class App extends Component {
  componentDidMount () {
    let log = createWriteStream(__dirname + '/../../../app.log')

    let devserver = spawn('skypager', ['dev'])

    devserver.on('error', (data) => {
      this.refs.devserver.add(data.toString())
      log.write(data.toString(), 'utf8')
    })

    devserver.stderr.on('data', (data) => {
      this.refs.devserver.add(data.toString())
      log.write(data.toString(), 'utf8')
    })

    devserver.stdout.on('data', (data) => {
      this.refs.devserver.add(data.toString())
      log.write(data.toString(), 'utf8')
    })
  }

  render () {
    return (
      <box label='Skypager'>
        <box ref="left" top="10%" left="2%" border={{type: 'line'}} style={{border:{fg:'cyan'}}}>
          <log ref='deepstream' />
        </box>
        <box ref="right" top="10%" left="53%" border={{type: 'line'}} style={{border:{fg:'cyan'}}}>
          <log ref='devserver' />
        </box>
      </box>
    )
  }
}

export default App



